.navbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 8px;
    background-color: teal;
    color: #fff;
  }
  
  .nav-links a {
    color: #fff;
    text-decoration: none;
  }
  
  .logo {
    font-size: 32px;
  }
  
  .menu {
    display: flex;
    gap: 1em;
    font-size: 18px;
  }
  .menu li{
    list-style: none;
  }
  
  .menu li:hover {
    background-color: #4c9e9e;
    border-radius: 5px;
    transition: 0.3s ease;
  }
  
  .menu li {
    padding: 5px 14px;
  }

  input[type=checkbox] {
    display: none;
  } 
  
  .hamburger {
    display: none;
    font-size: 24px;
    user-select: none;
  }

  .search input{
    font-size: 15px;
    padding: 5px 10px;
    outline: none;
    border-radius: 5px;
    border: none;
  }
  
  @media (max-width: 768px) {
   .menu {
      display:none;
      position: absolute;
      background-color:teal;
      right: 0;
      left: 0;
      text-align: center;
      padding: 16px 0;
    }
  
    .menu li:hover {
      display: inline-block;
      background-color:#4c9e9e;
      transition: 0.3s ease;
    }
  
    .menu li + li {
      margin-top: 12px;
    }
  
    input[type=checkbox]:checked ~ .menu {
      display: block;
    }
  
    .hamburger {
      display: block;
    }
  }